.slide-container {
  text-align: center;
  padding: 0;
}

.slide-container header {
  height: 34vh;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 8vh;
  padding: 10vh 1rem 1rem;
  display: flex;
}

.slide-container header h1 {
  margin: 0;
  font-size: 30px;
}

.slide-container header h2 {
  padding-top: .7rem;
  padding-bottom: .5rem;
  font-size: 22px;
}

.slide-container header h2 span {
  display: inline-block;
}

.slide-container header .time {
  color: var(--text-dark-a11y-color);
  font-size: .9rem;
  font-weight: 400;
}

.slide-container header .time .icon svg {
  height: 1rem;
  fill: var(--text-dark-a11y-color);
  margin-top: -3px;
  margin-right: 3px;
}

.slide-container header button {
  -webkit-user-select: none;
  user-select: none;
  cursor: pointer;
  background: unset;
  color: var(--link-color);
  border: none;
  margin-top: 5vh;
  font-size: min(5vw, 1.25rem);
  font-weight: 400;
}

.slide-container header button span.hide {
  opacity: 0;
}

.slide-container header button .link {
  font-size: min(5vw, 1.25rem);
}

.slide-container header button:hover:enabled span.link {
  text-decoration: underline;
}

.slide-container header button:disabled {
  cursor: default;
  color: var(--text-dark-a11y-color-50);
  text-decoration: none;
}

.slide-container .img-container {
  height: 50vh;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  padding: 1rem;
  display: flex;
  overflow: hidden;
}

.slide-container .img-container img {
  max-height: 100%;
  max-width: 100%;
}

.slide-container .img-container.hero {
  width: 100%;
  height: 58vh;
  background-position: 50%;
  background-size: cover;
  margin-top: 0;
}

.edit-mode .can-focus {
  cursor: pointer;
}

.edit-mode .has-focus {
  background: #fff8e6;
}

